import React, { useEffect } from "react";
import DaumPo from "./address.js";
import {
  Table,
  Form,
  InputGroup,
  Row,
  FloatingLabel,
  Col,
} from "react-bootstrap";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addCount, removeItem, disCount } from "../store.js";
import data from "../imgdata";
import Detail from "./detail.js";
import { Routes, Route, Link } from "react-router-dom";

function Cart(props) {
  let [open, setopen] = useState(false);
  let state = useSelector((state) => state);
  let dispatch = useDispatch();
  let [btncount, setbtncount] = useState(1);
  let [order, setorder] = useState(false);

  return (
    <div>
      <Table>
        <thead>
          <tr>
            <th></th>
            <th></th>

            <th></th>

            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((a, i) => {
            return (
              <tr key={i}>
                <td>{state.cart[i].name}</td>
                <td>{state.cart[i].count}</td>

                <td>
                  <button
                    className="order_btn"
                    onClick={() => {
                      dispatch(addCount(state.cart[i].id));
                    }}
                  >
                    +
                  </button>
                </td>
                <td>
                  <button
                    onClick={() => {
                      dispatch(removeItem(state.cart[i].id));
                    }}
                    className="order_btn"
                  >
                    X
                  </button>
                </td>
                <td>
                  <button
                    onClick={() => {
                      dispatch(disCount(state.cart[i].id));
                    }}
                    className="order_btn"
                  >
                    -
                  </button>
                </td>
                <td>{state.cart[i].price * state.cart[i].count}</td>
              </tr>
            );
          })}
        </tbody>
      </Table>
      <button
        className="btn-style margin-boot"
        onClick={() => {
          setopen(!open);
        }}
      >
         
      </button>
      {open === true ? <DaumPo></DaumPo> : null}

      <FormFloatingLayoutExample></FormFloatingLayoutExample>

      <Form.Select aria-label="Default select example">
        <option>   </option>
        <option value="1"></option>

        <option value="2">( )</option>

        <option value="3"></option>
      </Form.Select>

      <button
        className="btn-style margin-boot"
        onClick={() => {
          setorder(!order);
        }}
      >
        
      </button>
      {order === true ? (
        <div className="alert-box">  .</div>
      ) : null}
    </div>
  );
}

function FormFloatingLayoutExample() {
  return (
    <Row className="g-2">
      <Col md>
        <FloatingLabel
          controlId="floatingInputGrid"
          label="  ex)    000 000"
        >
          <Form.Control type="email" placeholder="" />
        </FloatingLabel>
      </Col>
    </Row>
  );
}

export default Cart;
